<template>
    <div class="top">
        <!-- <div class="logo"><span><van-image  :src="logo" /></span>行政综合管理</div> -->
        <div class="place">当前所在地区：<span>{{place}}</span></div>
        <div class="user"  @click="showPicker = true">切换地区<van-icon name="arrow-down" /></div>
         <van-popup v-model="showPicker" position="bottom">
            <van-picker
            title="选择地区"
            show-toolbar
            :columns="chose_place"
            @confirm="onConfirm"
            value-key="fcom_id"
            @cancel="showPicker = false"
            />
        </van-popup>
    </div>
</template>

<script>
import { apigetplace } from "@/api/index";
import { apiplacesave } from "@/api/index";
import storage from "@/assets/js/storage";
export default {
    data() {
        return {
            showPicker:false,
            chose_place:[],
            value: "",
            place:storage.get("place")
        }
    },
   
    methods:{
        getlist() {
            var _this=this
            apigetplace().then(res => {
                _this.chose_place = res.data.data;
            });
        },
        onConfirm(values){
            this.$dialog.confirm({
            title: '提示',
            message: '确定切换到【'+values.fcom_id+'】吗？',
            })
            .then(() => {
                // on confirm
                apiplacesave(values).then(res => {
                    if (res.data.code == 200) {
                        storage.set("place",values.fcom_id);
                        this.$dialog({ message: res.data.msg });
                        this.place=values.fcom_id;
                    }
                });
                this.showPicker=false
            })
            .catch(() => {
                // on cancel
                console.log(2)
                this.showPicker=false
                
            });
        }
    },
    created(){
        this.getlist();
    }
	
}
</script>

<style>

</style>



