<template>
    <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(piclist,index) in list" :key="index">
            <div class="banner"><img :src="piclist.img" alt=""></div>
        </van-swipe-item>
    </van-swipe>
</template>

<script>
export default {
    data() {
        return {
            list: [
                {img: require('../../../static/img/banner1.jpg')},
                {img: require('../../../static/img/banner2.jpg')},
                {img: require('../../../static/img/banner3.jpg')},
                {img: require('../../../static/img/banner4.jpg')},
                {img: require('../../../static/img/banner5.jpg')},
            ]
        }
    }
}
</script>

<style>
    
</style>



